body { 
  font-family: 'Nanum Gothic', sans-serif;
  font-size: 14px;
  margin: 0;
}

.right {
  float: right;
}

.left {
  float: left;
}
/* Landing Component Styles */

.landing-container {
    text-align: center;
    overflow: hidden;
    background-color: #000;
    min-height: 200px;
    height: 40vh;
    color: #FFF;
    font-size: 36px;
  }
  
  .logo img {
    height: 100px;
    width: auto;
    margin-top: 64px;
  }
  
  .logo .tagline {
    margin-top: 12px;
    letter-spacing: 1px;
    padding-left: 10px;
    font-size: 16px;
  }
  
  .landing-container .react-parallax {
    height: 100vh;
  }
  
  .pulse-down {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #FFFFFF;
    margin: 32px auto;
    text-align: center;
    display: none;
    -webkit-animation-name: pulse;
            animation-name: pulse;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s; 
    -webkit-animation-timing-function: ease-out; 
            animation-timing-function: ease-out; 
    -webkit-animation-delay: 0; 
            animation-delay: 0;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-fill-mode: none;
            animation-fill-mode: none;
    -webkit-animation-play-state: running;
            animation-play-state: running;
    cursor: pointer;
  }
  
  .pulse-down:active {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
    -webkit-animation: none;
            animation: none;
  }
  
  .pulse-down img {
    width: 25px;
    height: auto;
    font-size: 36px;
  }
  
  /* desktop media query */
  @media (min-width: 1025px){
    /* Landing Container Styles */
    .landing-container {
      width: 100vw;
      height: 100vh;
      display: table-cell;
      vertical-align: middle;
    }
  
    .landing-container .logo {
      vertical-align: middle;
      display: inline-block;
      -webkit-animation: fadein 2s;
              animation: fadein 2s;
    }
  
    .landing-container .logo img {
      height: 300px;
      width: auto;
      padding-top: 64px;
    }
  
    .landing-container .logo .tagline {
      font-size: 32px;
    }
  
    .pulse-down {
      display: block;
    }
  }
  
  /* Animations */
  @-webkit-keyframes pulse {
    0% {
      -webkit-transform: scale(0.7);
              transform: scale(0.7);
    }
  
    100% {
      -webkit-transform: scale(0.85);
              transform: scale(0.85);
    }
  }
  @keyframes pulse {
    0% {
      -webkit-transform: scale(0.7);
              transform: scale(0.7);
    }
  
    100% {
      -webkit-transform: scale(0.85);
              transform: scale(0.85);
    }
  }

  @-webkit-keyframes fadein {
    from {
      opacity: 0;
      -webkit-transform: scale(0.9);
              transform: scale(0.9);
    }
    to {
      opacity: 100;
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }

  @keyframes fadein {
    from {
      opacity: 0;
      -webkit-transform: scale(0.9);
              transform: scale(0.9);
    }
    to {
      opacity: 100;
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
#about .title {
