.time-event {
    text-align: center;
}

.time-event .line-container .title {
    text-align: left !important;
    margin-top: 0 !important;
    margin-left: 28px !important;
    margin-bottom: 24px !important;
    letter-spacing: -1px;
    font-size: 24px;
    line-height: 20px;
}

.time-event .line-container {
    border-left: 2px solid #93939393;
    margin: 0 16px;
    overflow: hidden;
}

.time-event .line-container.last {
    border: none;
}

.time-event .content-container {
    padding: 0;
}

.time-event .time-event-content-container {
    padding: 0 32px 48px 32px;
    overflow: hidden;
}

.time-event .line-dot {
    height: 21px;
    width: 21px;
    background-color: #EA617B;
    display: block;
    border-radius: 50%;
    float: left;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transition: -webkit-transform 0.5s;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
    position: relative;
    left: 11px;
}

.time-event:hover .line-dot {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    -webkit-transition: -webkit-transform 0.5s;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
}
  #financial-history {
      max-width: 800px;
      overflow: hidden;
      margin: 0 auto;
  }

  #financial-history svg {
      height: auto;
      width: 100%;
      display: block;
      margin: auto;
  }

  line {
      stroke: #eeeeee !important;
  }

  .chart-title {
      fill: #939393;
      text-transform: uppercase;
      font-size: 12px;
      font-weight: 500 !important;
  }

  .label {
      font-size: 8px;
      fill: #939393;
      font-weight: 700 !important;
      text-transform: uppercase;
      letter-spacing: 2px;
  }

  .bar {
      fill: #f197b3;
  }

  #financial-history text {
      fill: #939393;
      font-weight: 300;
  }

  #limit {
      stroke-dasharray: 5 !important;
      stroke: #f4a0b6 !important;
      z-index: 10;
  }

  .value {
