  }
#menu {
    overflow: hidden;
    position: fixed;
    top: 0;
    left: 0;
    box-shadow: 0px 1px 8px #00000033;
}

#menu-bar {
    width: 100vw;
    height: 100%;
    display: flex;
    align-items: center;
    height: 40px;
    width: 100vw;
    background-color: #FFFFFF;
}

.menu-items {
    background-color: #FFFFFF;
}

.hidden {
    display: none;
}

.branding {
    overflow: hidden;
    padding-left: 8px;
    display: flex;
    align-items: center;
}

.branding img {
    display: inline-block;
    height: 28px;
    width: auto;
    padding: 0 12px 0 8px;
}

.branding .tagline {
    vertical-align: middle;
    display: inline-flex;
    white-space: nowrap;
}

.hamburger-icon {
    width: 24px;
    height: auto;
    margin-left: auto;
    padding-right: 24px;
    cursor: pointer;
}

.menu-items {
    text-align: center;
}

.menu-item {
    width: 100%;
    display: block;
    padding: 8px 0;
}

.menu-item a {
    font-style: normal !important;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 11px;
    letter-spacing: 1px;
}

.desktop.menu-items {
    display: none;
}

/* desktop media query */
@media (min-width: 1025px){
    .desktop.menu-items {
        display: block !important;
        margin-left: auto;
    }

    .menu-item {
        display: inline-block;
        width: auto;
        padding: 0 16px;
    }

    .menu-item:last-of-type {
        margin-right: 32px;
    }

    .hamburger-icon {
        display: none;
    }
}
#VRFrame {
    margin: 36px auto;
    text-align: center;
    overflow: hidden;
}

iframe {
    display: none;
    width: 100%;
    height: 100%;
}

.vr-icon {
    height: 32px;
    width: auto;
    padding-bottom: 16px;
}

.full-screen-prompt {
    display: block;
}

/* desktop media query */
@media (min-width: 1025px){
    /* Landing Container Styles */
    #VRFrame {
        height: 60vh;
        width: 60vw;
    }

    iframe {
        display: block;
    }
    
    .full-screen-prompt {
        display: none;
    }
}
